import { Injectable } from '@angular/core';
import {
  IFieldDefinition,
  RX_RECORD_DEFINITION,
  RxFieldDefinitionService,
  RxRecordDefinitionService
} from '@helix/platform/record/api';
import { includes } from 'lodash';
import { IRecordDefinitionModel, IRecordFieldDefinitionModel } from '../../record-designer.types';

@Injectable({
  providedIn: 'root'
})
export class RxFieldDefinitionInspectorHelperService {
  constructor(
    private rxFieldDefinitionService: RxFieldDefinitionService,
    private rxRecordDefinitionService: RxRecordDefinitionService
  ) {}

  isFieldEditable(fieldDefinition: IFieldDefinition): boolean {
    return (
      !this.rxFieldDefinitionService.isReadOnly(fieldDefinition) &&
      !this.rxFieldDefinitionService.isJoinedField(fieldDefinition) &&
      this.rxFieldDefinitionService.isPropertiesCustomizationEnabled(fieldDefinition)
    );
  }

  isNameEditable(fieldDefinition: IFieldDefinition): boolean {
    return (
      !this.rxFieldDefinitionService.isInheritedField(fieldDefinition) &&
      this.rxFieldDefinitionService.isPropertiesCustomizationEnabled(fieldDefinition) &&
      !(fieldDefinition.lastUpdateTime && this.rxFieldDefinitionService.isOverlayMode(fieldDefinition))
    );
  }

  isDescriptionEditable(fieldDefinition: IFieldDefinition, definitionModel: IRecordDefinitionModel): boolean {
    if (this.rxRecordDefinitionService.isJoinRecord(definitionModel)) {
      return (
        includes(RX_RECORD_DEFINITION.joinRecordDefinitionCoreFieldIds, fieldDefinition.id) &&
        this.rxFieldDefinitionService.isPropertiesCustomizationEnabled(fieldDefinition)
      );
    } else {
      return (
        !this.rxFieldDefinitionService.isReadOnly(fieldDefinition) &&
        this.rxFieldDefinitionService.isPropertiesCustomizationEnabled(fieldDefinition)
      );
    }
  }

  isRequiredEditable(fieldDefinition: IFieldDefinition): boolean {
    const isAlwaysRequiredNonSystemField =
      !this.rxFieldDefinitionService.isExternalRecordField(fieldDefinition) &&
      includes(RX_RECORD_DEFINITION.alwaysRequiredNonSystemFieldIds, fieldDefinition.id);

    return this.isFieldEditable(fieldDefinition) && !isAlwaysRequiredNonSystemField;
  }

  isSubmitEditable(fieldDefinition: IFieldDefinition, definitionModel: IRecordDefinitionModel): boolean {
    return this.isFieldEditable(fieldDefinition) && !definitionModel.isAuditRecordDefinition;
  }

  arePermissionsEditable(fieldDefinition: IFieldDefinition, definitionModel: IRecordDefinitionModel): boolean {
    return (
      (fieldDefinition.id !== RX_RECORD_DEFINITION.coreFieldIds.id &&
        this.rxFieldDefinitionService.isPermissionsCustomizationEnabled(fieldDefinition) &&
        !(
          this.rxFieldDefinitionService.isJoinedField(fieldDefinition) &&
          fieldDefinition.id !== RX_RECORD_DEFINITION.coreFieldIds.displayId
        ) &&
        !definitionModel.isAuditRecordDefinition &&
        !includes(RX_RECORD_DEFINITION.AR_AUDIT_FIELD_IDS, fieldDefinition.id)) ||
      fieldDefinition.allowPermissionsEdit
    );
  }

  isAuditingEditable(fieldDefinition: IFieldDefinition, definitionModel: IRecordDefinitionModel): boolean {
    return (
      !this.rxFieldDefinitionService.isJoinedField(fieldDefinition) &&
      !this.rxFieldDefinitionService.isInheritedField(fieldDefinition) &&
      !definitionModel.isAuditRecordDefinition
    );
  }

  isFieldIdDisabled(fieldDefinition: IFieldDefinition, skipValidate = false): boolean {
    return (
      Boolean(fieldDefinition.lastUpdateTime) ||
      (!this.rxFieldDefinitionService.isJoinedField(fieldDefinition) &&
        (this.rxFieldDefinitionService.isReservedField(fieldDefinition, skipValidate) ||
          this.rxFieldDefinitionService.isInheritedField(fieldDefinition)))
    );
  }

  isDefaultValueEditable(fieldDefinition: IFieldDefinition, definitionModel: IRecordDefinitionModel): boolean {
    return (
      !(
        this.rxFieldDefinitionService.isReadOnly(fieldDefinition) &&
        fieldDefinition.id !== RX_RECORD_DEFINITION.coreFieldIds.displayId
      ) &&
      this.rxFieldDefinitionService.isPropertiesCustomizationEnabled(fieldDefinition) &&
      !this.rxRecordDefinitionService.isJoinRecord(definitionModel) &&
      !definitionModel.isAuditRecordDefinition
    );
  }

  isLengthEditable(fieldDefinition: IFieldDefinition, definitionModel: IRecordDefinitionModel): boolean {
    let isEditable = false;

    if (!this.rxRecordDefinitionService.isJoinRecord(definitionModel)) {
      let isReadonlyField = false;

      // As an exception, the Length of the Display ID field should be editable for Regular records (if customization is enabled)
      if (fieldDefinition.id !== RX_RECORD_DEFINITION.coreFieldIds.displayId) {
        isReadonlyField =
          this.rxFieldDefinitionService.isReadOnly(fieldDefinition) ||
          includes(RX_RECORD_DEFINITION.AR_CORE_FIELD_IDS, fieldDefinition.id);
      }

      isEditable =
        !isReadonlyField &&
        this.rxFieldDefinitionService.isPropertiesCustomizationEnabled(fieldDefinition) &&
        !definitionModel.isAuditRecordDefinition;
    }

    return isEditable;
  }

  isStoreEncryptedVisible(
    fieldDefinition: IRecordFieldDefinitionModel,
    definitionModel: IRecordDefinitionModel
  ): boolean {
    return (
      !this.rxRecordDefinitionService.isCustomRecord(definitionModel) &&
      !this.rxFieldDefinitionService.isExternalRecordField(fieldDefinition)
    );
  }

  isStoreEncryptedEditable(fieldDefinition: IFieldDefinition): boolean {
    return this.isFieldEditable(fieldDefinition) && !includes(RX_RECORD_DEFINITION.keyFieldIds, fieldDefinition.id);
  }

  isStoreHashedVisible(fieldDefinition: IFieldDefinition, definitionModel: IRecordDefinitionModel) {
    return (
      !this.rxRecordDefinitionService.isCustomRecord(definitionModel) &&
      !this.rxFieldDefinitionService.isExternalRecordField(fieldDefinition)
    );
  }

  isStoreHashedEditable(fieldDefinition: IFieldDefinition, definitionModel: IRecordDefinitionModel): boolean {
    return (
      this.isFieldEditable(fieldDefinition) &&
      !includes(RX_RECORD_DEFINITION.coreFieldIds, fieldDefinition.id) &&
      !definitionModel.isAuditRecordDefinition
    );
  }

  isNamedListEditable(fieldDefinition: IFieldDefinition, definitionModel: IRecordDefinitionModel): boolean {
    return (
      this.isFieldEditable(fieldDefinition) &&
      !includes(RX_RECORD_DEFINITION.keyFieldIds, fieldDefinition.id) &&
      !definitionModel.isAuditRecordDefinition
    );
  }

  isFieldMappingEditable(fieldDefinition: IFieldDefinition): boolean {
    return this.rxFieldDefinitionService.isPropertiesCustomizationEnabled(fieldDefinition);
  }
}
